<template>
  <div class="dialog">
    <!-- isShow props-->
    <div class="dialog-cover back"  v-if="isShow"  @click="closeMyself"></div>
    <!-- transition  -->
    <transition name="drop">
      <!--style props   -->
      <div class="dialog-content" style="margin: 0 auto" :style="{ top:topDistance+'px',width:widNum+'px',left:leftSite+'px',height:heightNum+'px'}"  v-if="isShow">
        <div style="width: 100%;text-align:right;">
          <slot name="header"></slot>
        </div>
<!--        <div class="dialog_head back">-->
<!--&lt;!&ndash;          &lt;!&ndash; title&ndash;&gt;&ndash;&gt;-->
<!--          <slot name="header"></slot>-->
<!--        </div>-->
<!--        <div class="ztDialogClass" :style="{paddingTop:pdt+'px',paddingBottom:pdb+'px'}">-->
          <!---->
          <slot name="main"></slot>
<!--        </div>-->
        <!---->
        <div  class="foot_close" @click="closeMyself">
          <div class="close_img back"></div>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
  export default {
    name: 'ZtDialog',
    props: {
      ztDialogClass:{
        type:String,
        default:'',
      },
      isShow: {
        // 
        type: Boolean,
        default: false,
        required:true, //
      },
      //div html
      //  :style="{top:topDistance+'%',width:widNum+'%'}"
      widNum:{
        //
        type: String,
        default:'86.5'
      },
      heightNum:{
        type:String,
        default:'300'
      },
      leftSite:{
        // 
        type: String,
        default:'0'
      },
      topDistance: {
        //top
        type: String,
        default:'35'
      },
      pdt:{
        //padding
        type: String,
        default:"0"
      },
      pdb:{
        //padding
        type: String,
        default:'0'
      }
    },
    methods:{
      closeMyself() {
        this.$emit("on-close");
        //"on-close"
      }

    }
  }
</script>

<style lang="scss" scoped>
 /* position */
  .dialog {
    position: relative;
    color: #2e2c2d;
    font-size: 16px;
  }
  /* z-index  */
  .dialog-cover {
    background: rgba(0,0,0, 0.8);
    position: fixed;
    z-index: 200;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  /*  z-index*/
  .dialog-content{
    position: fixed;
    margin: 0 auto;
    /*felx */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 300;
    background-color: #ffffff;
    .dialog_main {
      /*// */
      height: 100%;
    }
  }


</style>





