<script>
  import {Message} from 'element-ui';

  export default {
    data() {
      return {
        typeItems: [{
          value: 1,
          name: ''
        }, {
          value: 2,
          name: ''
        }, {
          value: 3,
          name: ''
        }, {
          value: '',
          name: ''
        }],

        tap:1,
        // 
        pickerOptions2: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        value4: [new Date(2000, 10, 10, 10, 10), new Date(2000, 10, 11, 10, 10)],
        value5: '',

        //
        keyMaterialwords: '',
        //
        dialogMaterialVisible: false,

        dialogMaterialTitle: '',
        //
        ordersList: [],
        keywords: '',
        //
        totalCount: -1,
        currentPage: 1,
        currentItemPage: 1,
        totalItemCount: -1,
        tableLoading: false,
        manageStatus: '1',
        defaultProps: {
          value: 'id',
          label: 'name',
          children: 'child'
        },
        products: [],
        clients: [],
        levels: [{
          id: 1,
          label: ''
        }, {
          id: 2,
          label: ''
        }, {
          id: 3,
          label: ''
        }, {
          id: 4,
          label: ''
        }, {
          id: 5,
          label: ''
        }],
        colorlist: [],
        specslist: [],
        dialogFormVisible: false,
        salse: {
          id: 0,
          clientId: [],
          clientName: '',
          quantity: '',
          note: '',
          expectLevel: '',//
          productName: '',
          proId: '', //Id
          specification: "", //
          mark: '', //
          productNo: '', //
          specsId: "",//Id
          colorId: '',
          progressId: 0,//Id;
        },
        selectMaterial: {},
        selectedM: {},
        currentMaterialPage: 1,
        dialogOrdersTitle: '',
        dialogordersVisible: false,
        order: {
          id: 0,
          contractName: '',
          contractNumber: '',
          orderNo: '',
          hasContract: 1,
          empId: '',
          cliId: '',
          qualityDeposit: '',
          tax: '',
          totalMoney: '',//
          signContractDate: '',
          state: '',//
          notes: '',
          clientArr:''
        },
        emps: [],
        //
        salesItem: {},
        orderItem: {
          id: 0,
          sid: 0,
          productNo: '',
          unitPrice: '',
          totalMoney: '',
          address: '',
          remarks: '',
          resourcesNumber: '',
          productionPlanDetailsIds: '',
        },
        dialogUnsalesTitle: '',
        dialogUnSaleVisible: false,
        keyItemwords: '',
        selectSales: {},
        selectedSales: {},
        orderitems: [],
        maxId: 0,
        items: [],
        deItem: [],
        manageStatus: '1',
        //
        advanceSearchViewVisible: false,
        faangledoubleup: 'fa-angle-double-up',
        faangledoubledown: 'fa-angle-double-down',
        keyproductName:'',
        keyClientName:'',
        keyempName:'',
        startDate:'',
        endDate:'',
    }
    },
    computed: {
      totalCountItem: function () {
        if (this.orderItem.unitPrice && this.orderItem.productNo) {
          this.orderItem.totalMoney = this.orderItem.unitPrice * this.orderItem.productNo;
          return this.orderItem.totalMoney;
        }
      }
    },
    mounted: function () {
      this.initData();
    },
    methods: {
      showAdvanceSearchView() {
        this.advanceSearchViewVisible = !this.advanceSearchViewVisible;
        this.keywords = '';
        if (!this.advanceSearchViewVisible) {
          this.emptyData();
          this.loadData();
        }
      },
      cancelSearch() {
        this.advanceSearchViewVisible = false;
        this.emptyData();
        // this.beginDateScope = '';
        this.loadData();
      },
      chooseSales() {

        this.loadSalse();
      },
      //
      chooseUnSale() {
        this.orderItem.productNo =this.selectSales.salesPlan.quantity
        this.dialogUnSaleVisible = false;
      },
      //
      addSalesItem() {
        let that=this;
        if (this.orderItem.productNo === '' ||
          this.orderItem.productNo === 0) {
          Message('');
          return false;
        }
        if (!this.selectSales.id) {
          Message("");
          return false;
        }
        let flag = false;
        if (this.items && this.items.length) {
          for (let i = this.items.length - 1; i >= 0; i--) {
            let me = that.items[i];
            if (me.productionPlanDetailsIds === this.selectSales.id) {
              me.productNo = that.orderItem.productNo;
              me.totalMoney = that.orderItem.totalMoney;
              me.unitPrice = that.orderItem.unitPrice;
              if (me.tap === 'ood') {
                me.tap = 'update';
              }
              flag = true;
            }
          }
        }
        if (!flag) {
          this.orderItem.productionPlanDetailsIds = this.selectSales.id;
          this.orderItem.tap = 'new';
          this.maxId++;
          this.orderItem.resourcesNumber = this.maxId;
          this.orderItem.sid = this.maxId;
          this.items.push(this.orderItem);
        }
        this.order.totalMoney=0;

        this.items.map((item)=>{that.order.totalMoney+=item.totalMoney; })
        this.clearItem();


      },
      clearOderAddloadDate(){
         this.clearItem();
         this.clearOder();
        this.items=[];
        this.loadmyData()
      },
      clearItem() {
        this.orderItem = {
          id: 0,
          sid: 0,
          productNo: '',
          unitPrice: '',
          totalMoney: '',
          address: '',
          remarks: '',
          resourcesNumber: '',
          productionPlanDetailsIds: '',
          // endDate:''
        };

        this.selectSales = {};
        this.selectedSales = {};
      },
      clearOder() {
        this.order = {
          id: 0,
          contractName: '',
          contractNumber: '',
          orderNo: '',
          hasContract: 1,
          empId: '',
          cliId: '',
          qualityDeposit: '',
          tax: '',
          totalMoney: '',//
          signContractDate: '',
          state: '',//
          notes: '',
          clientArr:''

        };
        this.maxId = 0;
      },
      //
      deleteItem(index, row) {
        if (row.tap !== 'new') {
          this.deItem.push(row);
        }
        this.items.splice(index, 1);

        if (this.items && this.items.length) {
          for (let i = this.items.length - 1; i >= 0; i--) {
            this.items[i].resourcesNumber = i + 1;
            this.items[i].sid = i + 1;
          }
        }
      },
      //
      editeItem(index, row) {
        console.log("row" + row);
        this.orderItem = row;
        this.selectSales.id = row.productionPlanDetailsIds
      },
      //
      editOder(index, row) {
        let that = this;
        console.log(row);
        this.order.id=row.id;
        this.order.contractName=row.contractName;
        this.order.contractNumber=row.contractNumber;
        this.order.orderNo=row.orderNo;
        this.order.hasContract=row.hasContract?1:0;
        this.order.empId=row.empId;
        this.order.qualityDeposit=row.qualityDeposit;
        this.order.tax=row.tax;
        this.order.totalMoney=row.totalMoney;
        this.order.signContractDate=row.signContractDate;
        this.order.state=row.orderStatus;
        this.order.notes=row.notes;
        this.order.clientArr = row.clientArr;
        this.order.cliId = row.clientArr.split(',').map(item=> {return  parseInt(item);} );
         //
        let itemsArr=[];
        this.dialogOrdersTitle = '';
        if (row.salesOrderDetails && row.salesOrderDetails.length) {
          itemsArr=row.salesOrderDetails.map((item,index,arr)=>{
               let  itemDetail={
                 tap:"ood",
                 sid:index+1,
                 productNo:item.productNo,
                 unitPrice:item.unitPrice,
                 totalMoney:item.totalMoney,
                 address:item.address,
                 remarks:item.remarks,
                 resourcesNumber:item.resourcesNumber,
                 productionPlanDetailsIds:item.productionPlanDetails.id,
               };
               return itemDetail;

          });
          that.maxId = row.salesOrderDetails.length;
        }
        this.items = itemsArr;
        this.dialogordersVisible = true;
      },
      used(item){
        // 
        this.handleClick('','',item.value+"")
      },
      handleClick(e,cli,status) {
        console.log(status);
        let code;
        if(status&&status!==''){
          code=status;
          this.activePanel=status;
        }else{
          code=this.activePanel;
        }
        this.tap=2;
        switch (code) {
          case "1":
            this.manageStatus = "1";
            break;
          case "2":
            this.manageStatus = "2";
            break;
          case "3":
            this.manageStatus = "3";
            break;
          case "4":
            this.manageStatus = "4";
            break;
          case "7":
            this.manageStatus = "";
            break;
          default:
            break;
        }
        this.loadmyData();
      },
      //
      addOrder(addOrderForm) {
        let _this = this;
        if (!this.items.length) {
          Message('@#');
          return false;
        }
        let plans = _.cloneDeep(this.order);
        plans.clientArr = plans.cliId.join(',');
        plans.cliId = plans.cliId.pop();
        let currentDetail = this.items.concat(this.deItem);
        plans.orderDetails = this.jsonToString(currentDetail);
        this.$refs[addOrderForm].validate((valid) => {
          if (valid) {
            if (plans.id) {
              //
              this.tableLoading = true;
              console.log(this.plan);
              this.postRequest("/orders/update", plans).then(resp => {
                _this.tableLoading = false;
                let re=resp || resp.status === 200 || resp.data.success;
                if (!re) {
                  _this.errorInfo();
                }
                _this.dialogordersVisible = false;
              })
            } else {
              //
              this.postRequest("/orders/add", plans).then(resp => {
                _this.tableLoading = false;
                let re=resp || resp.status === 200 || resp.data.success;
                if (!re) {
                  _this.errorInfo();
                }
                _this.dialogordersVisible = false;
              })
            }
          } else {
            return false;
          }
        });


      },
      errorInfo(){
        this.$message({message:'',type:'warning'})
      },

      //
      delOder(row){
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.doDelete(row.id);
        }).catch(() => {
          Message('');
        });
      },
      doDelete(ids){
        this.tableLoading = true;
        let _this = this;
        this.getRequest("/orders/delete?id=" + ids).then(resp=> {
          _this.tableLoading = false;
          if (resp && resp.status === 200&&resp.data.success) {
            _this.loadmyData();

          }else{
            Message("");
          }
        })
      },
      //
      Contact() {
        this.dialogOrdersTitle = '';
        this.dialogordersVisible = true;
      },
      //
      cancelchoose: function () {
        console.log('');
        // this.clearOder();
        // this.clearItem();

        this.selectMaterial = {};
        this.selectedM = {};
      },
      searchMaterialData() {
        this.loadMaterial();
      },
      searchItemData() {
        this.loadSalse();
      },
      //
      currentChange(currentChange) {
        this.currentPage = currentChange;
        console.log(this.currentPage);
        this.loadmyData();
      },
      currentItemChange() {
        this.currentItemPage = currentChange;
        console.log(this.currentItemPage);
        this.loadSalse();
      },
      keywordsItemChange() {
        this.loadSalse();
      },

      currentSelectShop(e) {
        console.log(e + '');
        this.salse.clientId = Array.from(e);
      },
      currentSelectCliets(e) {
        this.order.cliId = Array.from(e);
      },
      //**.....
      //
      chooseMaterial() {
        console.log('6666');
        let vm = this;
        if (this.selectMaterial.id > 0) {
          const msg = this.selectMaterial.producteName + "" + this.selectMaterial.specName + ";";
          this.$confirm('' + msg + "", '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            vm.setDetailData(vm);
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        } else {
          this.$message({
            type: 'info',
            message: ''
          })
        }

      },
      setDetailData() {
        let that = this;
        let v = that.selectMaterial;
        console.log(v);

        that.salse.productName = v.producteName;
        that.salse.proId = v.id; //Id
        // let speclist = [];
        //
        // let specsArr = v.specs;
        // if (specsArr.length) {
        //   specsArr.forEach(function (v) {
        //     speclist.push(v.id);
        //   })
        // }
        that.salse.specification = '';
        that.specslist = v.specs || [];
        that.colorlist = v.colors || [];
        that.salse.productNo = v.proType;
        this.dialogMaterialVisible = false;
      },
      currentMaterialChange(currentChange) {
        this.currentMaterialPage = currentChange;
        this.loadMaterial();
      },
      showRow(row) {
        this.selectedM = this.materialList.indexOf(row);
        this.selectMaterial = row;
        console.log(this.selectMaterial);
      },
      showitemRow(row) {
        this.selectedSales = this.orderitems.indexOf(row);
        this.selectSales = row;
        console.log(this.selectSales);
      },
      showMaterial() {
        this.loadMaterial();
      },
      keywordsMaterialChange() {
        if (val === '') {
          this.loadMaterial();
        }
      },
      loadMaterial() {
        let _this = this;
        this.tableLoading = true;
        this.getRequest("/product/findbypage?page=" + this.currentMaterialPage + "&size=10&queryName=" + this
          .keyMaterialwords).then(resp => {
          this.tableLoading = false;
          if (resp && resp.status === 200&&resp.data.success) {
            _this.materialList = resp.data.data;
            _this.totalMaterialCount = resp.data.total;
            _this.dialogMaterialVisible = true;
            // _this.emptyData();
            // 
          }else{
            Message("");
          }
        })
      },
      newSalePlan() {
        this.dialogTitle = "";
        let that = this;
        that.dialogFormVisible = true;
      },
      addSales(addSaleForm) {
        this.dialogFormVisible = true;
        let _this = this;
        let plans = _.cloneDeep(this.salse);
        plans.pcCode = plans.clientId.join(",");
        plans.clientId = plans.clientId.pop();
        this.$refs[addSaleForm].validate((valid) => {
          if (valid) {
            if (plans.id) {
              //
              this.tableLoading = true;
              console.log(_this.plan);
              this.postRequest("/sales/update", plans).then(resp => {
                _this.tableLoading = false;
                if (resp && resp.status === 200) {
                  // let data = resp.data;
                  _this.dialogFormVisible = false;
                  _this.loadmyData();
                  _this.emptyData();
                }
              })
            } else {
              //
              this.tableLoading = true;
              if (plans.clientId === 0 || plans.clientId === '') {
                plans.clientId = 0;
              }
              console.log(plans);
              this.postRequest("/sales/add", plans).then(resp => {
                _this.tableLoading = false;
                if (resp && resp.status === 200 && resp.data.success) {
                  _this.dialogFormVisible = false;
                  _this.loadmyData();
                  _this.emptyData();
                }
              })
            }
          } else {
            return false;
          }
        });

      },
      cancelSales() {
        this.dialogFormVisible = false;
        this.loadmyData();
        this.emptyData();
      },
      emptyData() {
        this.colorlist = [];
        this.specslist = [];
        this.salse = {
          id: 0,
          clientId: '',
          clientName: '',
          quantity: '',
          note: '',
          productName: '',
          proId: '', //Id
          specification: "", //
          mark: '', //
          productNo: '', //
          specsId: '',//Id
          colorId: '',
          progressId: 0,//Id;
        };

      },

      initData() {
        let _this = this;
        this.loadmyData();
        this.getRequest("/productionplan/basedata").then(resp => {
          if (resp && resp.status === 200) {
            let data = resp.data;
            _this.clients = _this.toTree(data.root.clients);
            _this.emps = data.root.emps;
            console.log(_this.clients);
            let pro = [];
            let p = data.root.products;
            p.forEach(function (v) {
              pro.push({
                id: v[0],
                producteName: v[1],
                specification: v[2],
                proType: v[3],
                mark: v[4],
                color: v[5],
              })
            });
            _this.products = pro;
          }
        })
      },
      searchData() {
        console.log(this.value5);
        if (this.value5 && this.value5.length) {
          this.startDate = this.formatDateTime(this.value5[0]);
          this.endDate = this.formatDateTime(this.value5[1]);
          console.log(this.startDate);
        }
        this.loadmyData();
      },
      loadmyData() {
        let _this = this;
        this.tableLoading = true;
        this.getRequest( "/orders/findbypage?page="+ this.currentPage
          + "&size=10&queryName="+ this.keywords
          +"&status="+ this.manageStatus
          +"&empName="+ this.keyempName
          +"&productName="+ this.keyproductName
          +"&clientName="+ this.keyClientName
          +"&start="+ this.startDate
          +"&end="+ this.endDate
           ).then(resp => {
          this.tableLoading = false;
          if (resp && resp.status === 200 && resp.data.success) {
            _this.ordersList = resp.data.data;
            console.log(_this.ordersList);
            _this.totalCount = resp.data.total;
            _this.emptyData();
          }
        })
      },
      //()
      loadSalse() {
        let _this = this;
        let clientid='';
        this.tableLoading = true;
        if (this.order.cliId && this.order.cliId.length) {
          clientid = this.order.cliId[this.order.cliId.length-1];
        }
        if (clientid==='') {
          Message("@--");
          return false;
        } else {
          this.getRequest("/productionplandetails/findForOrder?page="
            + this.currentItemPage
            + "&size=10&clientId=" + clientid
          ).then(resp => {
            this.tableLoading = false;
            if (resp && resp.status === 200 && resp.data.success) {
              _this.orderitems = resp.data.data;
              _this.totalItemCount = resp.data.total;
              _this.dialogUnSaleVisible = true;
            }else{
              Message('')
            }
          })
        }
      },
      toTree(data) {
        let result = [];
        if (!Array.isArray(data)) {
          return result
        }
        data.forEach(item => {
          delete item.child;
        });
        let map = {};
        data.forEach(item => {
          map[item.id] = item;
        });
        data.forEach(item => {
          let parent = map[item.parentClientId];
          if (parent) {
            (parent.child || (parent.child = [])).push(item);
          } else {
            result.push(item);
          }
        });
        console.log(result);
        return result;
      }
    },


  }

</script>
<style>
  .send-input {
    width: 160px;
  }

  .send-head {
    padding: 0;
    display: flex;
    justify-content: space-between;
    align-items: center
  }

  .send-head-right-btn {
    margin-left: 5px;
    margin-right: 20px;
    display: inline
  }

  .send-head-left-btn {
    margin-left: 5px
  }

  .send-main {
    padding-left: 0px;
    padding-top: 0px

  }

  .send-main-div {
    margin-bottom: 10px;
    border: 1px;
    border-radius: 5px;
    border-style: solid;
    padding: 5px 0px 5px 0px;
    box-sizing: border-box;
    border-color: #20a0ff
  }
  /**/
  .stamp {
    color: #2783ca;
  }
  .divce-mode {
    display: flex;
    flex-direction: column;
    padding: 0 20px;
  }
  .product-model-base {
    width: 100%;
    height: auto;
    display: flex;
    padding: 0 20px 20px 20px;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: start;
    box-sizing: border-box;
    background-color: #E5EFF1;
  }

  .product-model-item {
    box-sizing: border-box;
    height: 140px;
    width: 140px;
    overflow: hidden;
    position: relative;
    margin-top: 10px;
    color: #ffffff;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    background-color: #2fc5da;
    margin-left: 25px;
    padding: 30px;
    box-shadow: 2px 4px 20px #005377;
    cursor: pointer;
  }

  /*************************************************************/
  .el-input__inner {
    -webkit-appearance: none;
    background-color: #FFF;
    background-image: none;
    border-radius: 4px;
    /*border: 1px solid #DCDFE6;*/
    border: none;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    color: #606266;
    display: inline-block;
    font-size: inherit;
    height: 40px;
    line-height: 40px;
    outline: 0;
    padding: 0 15px;
    -webkit-transition: border-color .2s cubic-bezier(.645, .045, .355, 1);
    transition: border-color .2s cubic-bezier(.645, .045, .355, 1);
    width: 100%;
  }

  .el-input__inner:focus {
    -webkit-appearance: none;
    background-color: #FFF;
    background-image: none;
    border-radius: 4px;
    border: 1px solid #66B1FF;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    color: #606266;
    display: inline-block;
    font-size: inherit;


    outline: 0;
    padding: 0 15px;
    -webkit-transition: border-color .2s cubic-bezier(.645, .045, .355, 1);
    transition: border-color .2s cubic-bezier(.645, .045, .355, 1);
    width: 100%;
  }

  .el-input--prefix .el-input__inner:focus {
    padding-left: 30px;
  }

  .el-textarea__inner {
    width: 200px;
    border: none;
  }

  .el-textarea__inner:focus {
    display: block;
    resize: vertical;
    padding: 5px 15px;
    line-height: 1.5;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 100%;
    font-size: inherit;
    color: #606266;
    background-color: #FFF;
    background-image: none;
    border: 1px solid #66B1FF;
    border-radius: 4px;
    -webkit-transition: border-color .2s cubic-bezier(.645, .045, .355, 1);
    transition: border-color .2s cubic-bezier(.645, .045, .355, 1);
  }

  .el-input__icon:after {
    display: none;
  }

</style>



